.primary {
  background: #06038d;
  padding: 10px 20px;
  text-decoration: none;
  color: white;
  font-weight: bold;
  transition: 0.5s;
  border-radius: 5px;
  border: 1px solid #06038d;
  margin-right: 20px;
  text-transform: uppercase;
}
.primary:hover {
  background: #02005a;
  color: white;
  border: 1px solid #02005a;
}

.secondary {
  background: #78b3e0;
  padding: 10px 20px;
  text-decoration: none;
  color: #000;
  font-weight: bold;
  transition: 0.5s;
  border-radius: 5px;
  border: 1px solid #78b3e0;
  margin-right: 20px;
  text-transform: uppercase;
}
.secondary:hover {
  background: #5494c4;
  color: #000;
  border: 1px solid #5494c4;
}

.tertiary {
  background: transparent;
  padding: 10px 20px;
  text-decoration: none;
  color: #02005a;
  font-weight: bold;
  transition: 0.5s;
  border-radius: 5px;
  border: 1px solid #02005a;
  margin-right: 20px;
  text-transform: uppercase;
}
.tertiary:hover {
  background: #02005a;
  color: #fff;
  border: 1px solid #02005a;
}

.inactive {
  background: #a2a2a2 !important;
  color: #000;
  padding: 10px 20px;
  text-decoration: none;
  font-weight: bold;
  transition: 0.5s;
  border-radius: 5px;
  border: 1px solid transparent;
  pointer-events: none;
  cursor: not-allowed;
  text-transform: uppercase;
}

.inactive:hover {
  color: #02005a;
}

